import React from "react"
import { HIs } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
export default function MTCTestCanEdit(props) {
  const ctc = useConfigTableCell({ props })
  const { cell } = props
  return (
    <div
      className="mct-readonly"
      style={ctc.styleWrap(
        {},
        ctc.canEdit === true
          ? { background: "#4dbd74" }
          : { background: "#f64e60" }
      )}
    >
      {ctc.canEdit === true ? "Can Edit" : "Disabled"}
    </div>
  )
}
